import React, { Component } from "react";
import { Form } from "react-bootstrap";
import Title from "./Title";
import axios from "axios";
import alertrict from "alertrict";

export default class Message extends Component {
  state = {
    name: "",
    message: "",
    endpoint: "https://idul-fitri.herokuapp.com/api/v1",
    // endpoint: "http://localhost:8000/api/v1",
    key: "3dcce7ab-d511-4cfe-a62e-b3089444ee4d",
  };

  onNameChange = (e) => {
    this.setState({
      name: e.target.value,
    });
  };

  onMessageChange = (e) => {
    this.setState({
      message: e.target.value,
    });
  };

  submitHandler = async (e) => {
    e.preventDefault();
    
    const confirmation = await alertrict({
      title: "Konfirmasi",
      text: "Apakah anda yakin ingin mengirim pesan?",
      confirmText: "Ya",
      cancelText: "tidak",
      showCancel: true,
    });
    
    if (confirmation.isConfirmed) {
      document.getElementById("message-submit").disabled = true;
      setTimeout(
        () => (document.getElementById("message-submit").disabled = false),
        5000
      );
      document.getElementsByTagName("body")[0].classList.add("preloading");
      document.getElementsByClassName("preload")[0].classList.add("active");

      let userData = new FormData();
      userData.append("_apikey", this.state.key);
      userData.append("from", this.state.name);
      userData.append("message", this.state.message);

      const send = await axios
        .post(`${this.state.endpoint}/greeting`, userData)
        .catch((e) => console.log(e));

      if (send) {
        document
          .getElementsByClassName("preload")[0]
          .classList.remove("active");
        document.getElementsByTagName("body")[0].classList.remove("preloading");
        var message = `Terima kasih ${this.state.name}, pesan anda berhasil dikirim. Lihat kartu ucapan?`;
        const alert = await alertrict({
          title: "Berhasil!",
          text: message,
          confirmText: "Ya",
          cancelText: "tidak",
          showCancel: true,
        });

        if (alert.isConfirmed) {
          window.location.href = `/idul-fitri/#/card/${this.state.name}`;
        } else {
          window.location.href = "/idul-fitri/";
        }
      }
    }
  };

  render() {
    return (
      <section id="section-message" className={this.props.className}>
        <div className="wrapper">
          <div className="row">
            <div className="col-12">
              <Title text="Message" />
              <p className="fs-14 fs-md-16 text-center mb-0">
                Kirim pesan dan ambil kartu ucapan 💌.
              </p>
            </div>
          </div>
          <div className="row mt-4 justify-content-center">
            <div className="col-11 col-md-8 col-lg-6">
              <Form onSubmit={this.submitHandler}>
                <Form.Group>
                  <Form.Label>Nama</Form.Label>
                  <Form.Control
                    onChange={this.onNameChange}
                    className="text-black"
                    type="text"
                    placeholder="Masukkan nama lengkap ..."
                    required="required"
                  />
                </Form.Group>

                <Form.Group className="mt-4">
                  <Form.Label>Pesan</Form.Label>
                  <Form.Control
                    onChange={this.onMessageChange}
                    className="text-black"
                    as="textarea"
                    rows={3}
                    placeholder="Masukkan pesan ..."
                    required="required"
                  />
                </Form.Group>
                <div className="d-flex justify-content-end mt-4">
                  <button
                    type="submit"
                    id="message-submit"
                    className="button-custom font-weight-500"
                  >
                    Kirim{" "}
                    <div className="button-icon">
                      <i className="ri-send-plane-2-fill"></i>
                    </div>
                  </button>
                </div>
              </Form>
            </div>
          </div>
        </div>
      </section>
    );
  }
}
