import React, { Component } from "react";

import {
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

import './css/app.min.css';
import GreetingCard from "./routes/GreetingCard";
import Home from "./routes/Home";
import Preload from "./components/Preload";

export default class App extends Component {
  componentDidMount() {
    document.getElementsByTagName('body')[0].classList.add('preloading')
    document.getElementsByClassName('preload')[0].classList.add('active')
    window.addEventListener('load', function () {
      document.getElementsByClassName('preload')[0].classList.remove('active')
      document.getElementsByTagName('body')[0].classList.remove('preloading')
    })
  }

  render() {
    return (
      <>
        <Router>
          <Switch>
            <Route path={["/card/:name", "/idul-fitri/card/:name"]}>
              <GreetingCard />
            </Route>
            <Route path={["/", "/idul-fitri/", "/idul-fitri"]}>
              <Home />
            </Route>
          </Switch>
        </Router>
        <Preload className="active" />
      </>
    );
  }
}
